import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import InputLabel from "@material-ui/core/InputLabel";
import FormControl from "@material-ui/core/FormControl";
import Select from "@material-ui/core/Select";

const useStyles = makeStyles((theme) => ({
  formControl: {
    margin: theme.spacing(0),
    minWidth: 120,
  },
  selectEmpty: {
    marginTop: theme.spacing(2),
  },
}));

export default function NativeSelects(setTimeAttendance) {
  const classes = useStyles();
  const [state, setState] = React.useState({
    location: "RS Tower",
  });

  const handleChange = (event) => {
    const name = event.target.name;
    setState({
      ...state,
      [name]: event.target.value,
    });
    setTimeAttendance({
      ...state,
      [name]: event.target.value,
    });
  };

  return (
    <div>
      <FormControl
        className={classes.formControl}
        style={{
          color: "white",
          backgroundColor: "rgba(0, 0, 0, 0.5)",
          margin: "0px",
          borderRadius: "5px",
          width: "100%",
        }}
      >
        <InputLabel
          htmlFor="location"
          className="mt-1 ml-1"
          style={{ color: "white", margin: "0px" }}
        >
          <b>
            <i className="fas fa-map-marked-alt"></i> Select Workplace :
          </b>
        </InputLabel>
        <Select
          native
          value={state.location}
          onChange={handleChange}
          inputProps={{
            name: "location",
            id: "location",
          }}
          className="ml-1 mr-1"
          style={{
            color: "white",
          }}
        >
          <option
            value={"RS Tower"}
            style={{
              color: "white",
              backgroundColor: "rgba(0, 0, 0, 0.5)",
            }}
          >
            RS Tower
          </option>
          <option
            value={"Work at home"}
            style={{
              color: "white",
              backgroundColor: "rgba(0, 0, 0, 0.5)",
            }}
          >
            Work at home
          </option>
          <option
            value={"Other"}
            style={{
              color: "white",
              backgroundColor: "rgba(0, 0, 0, 0.5)",
            }}
          >
            Other
          </option>
        </Select>
      </FormControl>
    </div>
  );
}
