import http from "./http-common";
class TimeAttendanceService {
  getAllCurr(data) {
    return http.post("/gettimeAttendance", data);
  }

  create(data) {
    return http.post("/timeAttendance", data);
  }

  getTimeHistrory() {
    return http.post("/gettimeAttendanceHistory");
  }

  findByDate(title) {
    return http.get(`/timeAttendance=${title}`);
  }
}

export default new TimeAttendanceService();
