import React, { useState } from "react";
import { useForm } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup";
import * as yup from "yup";
import { useDispatch, useSelector } from "react-redux";
import { register2 } from "../../actions/auth";

import Header from "../../components/layouts/Header";
import { Form, Button, Row, Col } from "react-bootstrap";
// import { Button, Input } from "@material-ui/core";
const schema = yup.object().shape({
  userName: yup
    .string()
    .min(4, "Username minimum 6 character")
    .max(40)
    .required("Username is required"),
  password: yup
    .string()
    .min(6, "Password minimum 6 character")
    .max(40)
    .required("Password is required"),
  email: yup
    .string()
    .email("Please enter valid email")
    .required("Email is required"),
  firstName: yup
    .string()
    .matches(/^[A-Za-z ]*$/, "Please enter valid first name")
    .min(2, "Minimum 2 character")
    .max(40)
    .required("First name is required"),
  lastName: yup
    .string()
    .matches(/^[A-Za-z ]*$/, "Please enter valid last name")
    .min(2, "Minimum 2 character")
    .max(40)
    .required("Last name is required"),
});

export default function RegisterPage_RHF(props) {
  //   const { register, handleSubmit, errors } = useForm({
  //     //   resolver: yupResolver(schema),
  //   });
  const [successful, setSuccessful] = useState(false);

  const { message } = useSelector((state) => state.message);
  const dispatch = useDispatch();
  const { register, handleSubmit, setValue, errors } = useForm({
    resolver: yupResolver(schema),
  });

  const onSubmit = (data) => {
    console.log(data);
    dispatch(
      register2(
        data.userName,
        data.email,
        data.password,
        data.firstName,
        data.lastName
      )
    ).then(() => props.history.push("/"));
  };
  //   const [username, setUsername] = useState("");
  //   const [email, setEmail] = useState("");
  //   const [password, setPassword] = useState("");

  const handleChangeUsername = (e) => {
    setValue("userName", e.target.value);
  };

  const handleChangePassword = (e) => {
    console.log("password");
    setValue("password", e.target.value);
  };

  const handleChangeEmail = (e) => {
    setValue("email", e.target.value);
  };

  const handleChangeFirstName = (e) => {
    setValue("firstName", e.target.value);
  };

  const handleChangeLastName = (e) => {
    setValue("lastName", e.target.value);
  };

  const handleRegister = (e) => {
    e.preventDefault();

    // dispatch(register(username, email, password))
    //   .then(() => {})
    //   .catch(() => {});
  };

  React.useEffect(() => {
    register("userName");
    register("password");
    register("email");
    register("firstName");
    register("lastName");
    // custom register Antd input
  }, [register]);

  return (
    <div style={{ height: "100vh" }}>
      <Header />
      <div className="m-0 pb-4" style={{ paddingTop: "calc(1vh + 20px)" }}>
        <div
          className="form-area-register"
          style={{ padding: "calc(2vh + 10px)" }}
        >
          <div>
            <h4>Sign up</h4>
          </div>
          {/* <form onSubmit={handleSubmit(onSubmit)}>
            <input
              type="text"
              name="userName"
              onChange={handleChangeUsername}
            />
            <input type="submit" />
          </form> */}
          <form onSubmit={handleSubmit(onSubmit)}>
            <Form.Group
              as={Row}
              controlId="userName"
              ref={register}
              className="mb-3"
            >
              <Form.Label
                column
                sm={12}
                xl={4}
                className="m-0 pr-2"
                // style={{ textAlign: "right" }}
              >
                <i className="far fa-user"></i> Username :
              </Form.Label>
              <Col sm={12} xl={8} className="pl-0">
                <Form.Control
                  type="text"
                  placeholder="Enter Username"
                  className="p-0 px-2"
                  style={{
                    backgroundColor: "transparent",
                    color: "white",
                  }}
                  onChange={handleChangeUsername}
                />
                {errors.userName ? (
                  <div className="text-danger m-0">
                    {errors.userName?.message}
                  </div>
                ) : null}
              </Col>
            </Form.Group>

            <Form.Group
              as={Row}
              controlId="password"
              ref={register}
              className="mb-3"
            >
              <Form.Label
                column
                sm={12}
                xl={4}
                className="m-0 pr-2"
                // style={{ textAlign: "right" }}
              >
                <i className="fas fa-key"></i> Password :
              </Form.Label>
              <Col sm={12} xl={8} className="pl-0">
                <Form.Control
                  type="password"
                  placeholder="Enter Password"
                  className="p-0 px-2"
                  style={{
                    backgroundColor: "transparent",
                    color: "white",
                    // borderRadius: "0px",
                  }}
                  onChange={handleChangePassword}
                />
                {errors.password ? (
                  <div className="text-danger m-0">
                    {errors.password?.message}
                  </div>
                ) : null}
              </Col>
            </Form.Group>

            <Form.Group
              as={Row}
              controlId="email"
              ref={register}
              className="mb-3"
            >
              <Form.Label
                column
                sm={12}
                xl={4}
                className="m-0 pr-2"
                // style={{ textAlign: "right" }}
              >
                <i className="far fa-envelope"></i> Email :
              </Form.Label>
              <Col sm={12} xl={8} className="pl-0">
                <Form.Control
                  type="email"
                  placeholder="Enter your email"
                  className="p-0 px-2"
                  style={{
                    backgroundColor: "transparent",
                    color: "white",
                    // borderRadius: "0px",
                  }}
                  onChange={handleChangeEmail}
                />
                {errors.email ? (
                  <div className="text-danger m-0">{errors.email?.message}</div>
                ) : null}
              </Col>
            </Form.Group>

            <Form.Group
              as={Row}
              controlId="firstName"
              ref={register}
              className="mb-3"
            >
              <Form.Label
                column
                sm={12}
                xl={4}
                className="m-0 pr-2"
                // style={{ textAlign: "right" }}
              >
                First name :
              </Form.Label>
              <Col sm={12} xl={8} className="pl-0">
                <Form.Control
                  type="text"
                  placeholder="Enter your first name"
                  className="p-0 px-2"
                  style={{
                    backgroundColor: "transparent",
                    color: "white",
                    // borderRadius: "0px",
                  }}
                  onChange={handleChangeFirstName}
                />
                {errors.firstName ? (
                  <div className="text-danger m-0">
                    {errors.firstName?.message}
                  </div>
                ) : null}
              </Col>
            </Form.Group>

            <Form.Group
              as={Row}
              controlId="lastName"
              ref={register}
              className="mb-3"
            >
              <Form.Label
                column
                sm={12}
                xl={4}
                className="m-0 pr-2"
                // style={{ textAlign: "right" }}
              >
                Last name :
              </Form.Label>
              <Col sm={12} xl={8} className="pl-0">
                <Form.Control
                  type="text"
                  placeholder="Enter your last name"
                  className="p-0 px-2"
                  style={{
                    backgroundColor: "transparent",
                    color: "white",
                    // borderRadius: "0px",
                  }}
                  onChange={handleChangeLastName}
                />
                {errors.lastName ? (
                  <div className="text-danger m-0">
                    {errors.lastName?.message}
                  </div>
                ) : null}
              </Col>
            </Form.Group>

            <Button
              className="btn btn-primary btn-large centerButton"
              type="submit"
              style={{
                width: "100%",
                backgroundColor: "#3b5998",
                border: "none",
              }}
            >
              Sign Up
            </Button>
          </form>
        </div>
      </div>
    </div>
  );
}

