import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Router, Switch, Route, Link } from "react-router-dom";

import "./App.css";

import Home from "./components/Home";
import Profile from "./components/Profile";
import BoardUser from "./components/BoardUser";
import BoardModerator from "./components/BoardModerator";
import BoardAdmin from "./components/BoardAdmin";

import LoginPage from "./components/pages/LoginPage";
import RegisterPage from "./components/pages/RegisterPage/RegisterPage";
import TimeAttendancePage from "./components/pages/TimeAttendancePage";
import RegisterPage_RHF from "./components/pages/RegisterPage_RHF";

import { logout } from "./actions/auth";
import { clearMessage } from "./actions/message";

import { history } from "./helpers/history";

// Material UI
import { MuiThemeProvider } from "@material-ui//core/styles";
import { createMuiTheme } from "@material-ui/core/styles";
const App = () => {
  const [showModeratorBoard, setShowModeratorBoard] = useState(false);
  const [showAdminBoard, setShowAdminBoard] = useState(false);
  const [showManagerBoard, setShowManagerBoard] = useState(false);

  const { user: currentUser } = useSelector((state) => state.auth);
  const dispatch = useDispatch();

  useEffect(() => {
    history.listen((location) => {
      dispatch(clearMessage()); // clear message when changing location
    });
  }, [dispatch]);

  useEffect(() => {
    if (currentUser) {
      setShowModeratorBoard(currentUser.roles.includes("ROLE_MODERATOR"));
      setShowAdminBoard(currentUser.roles.includes("ROLE_ADMIN"));
      setShowManagerBoard(currentUser.roles.includes("ROLE_MANAGER"));
    }
  }, [currentUser]);

  const logOut = () => {
    dispatch(logout());
  };

  const theme = createMuiTheme({
    typography: {
      fontFamily: `"Roboto", "Helvetica", "Arial", sans-serif`,
      fontSize: 14,
      fontWeightLight: 300,
      fontWeightRegular: 400,
      fontWeightMedium: 500,
    },
  });

  return (
    <>
      <MuiThemeProvider theme={theme}>
        <div
          className="bg"
          style={{ backgroundImage: "url(/img/image_b2.jpg)" }}
        >
          <Router history={history}>
            <Switch>
              <Route exact path="/login" component={LoginPage} />
              <Route exact path="/register" component={RegisterPage_RHF} />
              <Route exact path="/profile" component={Profile} />
              <Route
                exact
                path="/TimeAttendance"
                component={TimeAttendancePage}
              />
              <Route exact path="/Register2" component={RegisterPage_RHF} />
              <Route path="/user" component={BoardUser} />
              <Route path="/mod" component={BoardModerator} />
              <Route path="/admin" component={BoardAdmin} />
              <Route path={"/home"} component={Home} />
              <Route path={["/"]} component={LoginPage} />
            </Switch>
          </Router>
        </div>
      </MuiThemeProvider>
    </>
  );
};

export default App;
