import React, { useState, useEffect } from "react";
import { useSelector } from "react-redux";

import Header from "../../layouts/Header";
import { Container, Row, Col, Button } from "react-bootstrap";
import MaterialTable from "material-table";
import moment from "moment";

import selectLocation_MUI from "../../fragment/selectBox_MUI";
// import selectLocation from "../../fragment/selectLocation";
import TimeAttendaceService from "../../../services/TimeAttendace.service";

const TimeAttendancePage = () => {
  const { user: currentUser } = useSelector((state) => state.auth);

  const initialTimeAttendance = {
    id: currentUser.id,
    location: "RS Tower",
  };
  const [sTimeAttendance, setTimeAttendance] = useState(initialTimeAttendance);
  const [sTimeAttendances, setsTimeAttendances] = useState([]);
  const [sHistoryData, setsHistoryData] = useState([]);
  const [Time, setTime] = useState("");
  useEffect(() => {
    retrieveTimeAttendances();
    retrieveTimeAttendancesHistory();
    // code to run on component mount
    setInterval(() => {
      setTime(moment().format("DD/MM/YYYY, H:mm:ss"));
    }, 1000);
  }, []);

  const retrieveTimeAttendances = () => {
    let data = {
      location: sTimeAttendance.location,
    };
    TimeAttendaceService.getAllCurr(data)
      .then((response) => {
        setsTimeAttendances(response.data);
        retrieveTimeAttendancesHistory();
        // console.log("sTimeAttendances : ");
        // console.log(response.data);
      })
      .catch((e) => {
        console.log(e);
      });
  };

  const retrieveTimeAttendancesHistory = () => {
    TimeAttendaceService.getTimeHistrory()
      .then((response) => {
        setsHistoryData(response.data);
        console.log("Time History : ");
        console.log(response.data);
      })
      .catch((e) => {
        console.log(e);
      });
  };

  const HandleCheckIn = () => {
    let data = {
      location: sTimeAttendance.location,
    };

    TimeAttendaceService.create(data)
      .then((response) => {
        console.log(response.data);
        retrieveTimeAttendances();
      })
      .catch((e) => {
        console.log(e);
      });
  };

  const setStatus = (status) => {
    if (status == "On Time") {
      return <b style={{ color: "#80b918" }}>{status}</b>;
    } else if (status == "Late") {
      return <b style={{ color: "red" }}>{status}</b>;
    }
    return <b>{status}</b>;
  };
  const TimeAttendanceHistoryColumns = [
    {
      title: "Date",
      field: "Date",
      headerStyle: {
        textAlign: "center",
        fontFamily: "B612",
      },
      render: (item) => (
        <div style={{ textAlign: "center" }}>
          {moment(item.date).format("D MMM")}
        </div>
      ),
    },
    {
      title: "Workplace",
      field: "location",
      headerStyle: {
        textAlign: "center",
      },
      cellStyle: { minWidth: 200, textAlign: "center" },
      // render: (item) => (
      //   <div style={{ textAlign: "center" }}>{item.location}</div>
      // ),
    },
    {
      title: "Time In",
      field: "timestamp",
      headerStyle: {
        textAlign: "center",
      },
      render: (item) => (
        <div style={{ textAlign: "center" }}>{item.timeIn}</div>
      ),
    },
    {
      title: "Time out",
      field: "timeOut",
      headerStyle: {
        textAlign: "center",
      },
      render: (item) => (
        <div style={{ textAlign: "center" }}>{item.timeOut}</div>
      ),
    },
    {
      title: "Status",
      headerStyle: {
        textAlign: "center",
      },
      render: (item) => (
        <div style={{ textAlign: "center" }}>{setStatus(item.status)}</div>
      ),
    },
  ];

  const TableHistory = () => {
    return (
      <MaterialTable
        title="Time Attendance History"
        columns={TimeAttendanceHistoryColumns}
        data={sHistoryData}
        // onRowClick={(evt, data) => clickTransactionRow(data)}
        options={{
          pageSize: 10,
          toolbar: false,
          search: false,
          sorting: false,
        }}
      />
    );
  };

  const timeAttenceColumns = [
    {
      title: "No",
      field: "timestamp_id",
      render: (item) => (
        <span>
          <b>{item.tableData.id + 1}</b>
        </span>
      ),
    },
    {
      title: "Workplace",
      cellStyle: { minWidth: 180 },
      headerStyle: {
        textAlign: "center",
      },
      render: (item) => (
        <div style={{ textAlign: "center" }}>{item.location}</div>
      ),
    },
    {
      title: "Date Check-In",
      field: "timestamp",
      cellStyle: { minWidth: 200 },
      headerStyle: {
        textAlign: "center",
      },
      render: (item) => (
        <div style={{ textAlign: "center" }}>
          {moment.utc(item.check_in).format("D/MM/YYYY")}
        </div>
      ),
    },
    {
      title: "Time Check-In	",
      field: "timestamp",
      cellStyle: { minWidth: 150 },
      headerStyle: {
        textAlign: "center",
      },
      render: (item) => (
        <div style={{ textAlign: "center" }}>
          {moment.utc(item.check_in).format("HH:mm")}
        </div>
      ),
    },
  ];

  const tableTimeAttenToday = () => {
    return (
      // <div>Test</div>
      <MaterialTable
        id="TimeAttendanceToday"
        title="TimeAttendanceToday"
        columns={timeAttenceColumns}
        data={sTimeAttendances}
        // data={timeattentionReducer.result ? timeattentionReducer.result : []}
        options={{
          pageSize: 10,
          toolbar: false,
          search: false,
          sorting: false,
        }}
      />
    );
  };

  return (
    <div style={{ height: "100%" }}>
      {/* <div className="bg_login"> */}
      <div>
        <Header />
        <div className="pt-3 px-4">
          <Container fluid style={{ width: "90vw", maxWidth: "1600px" }}>
            <Row>
              <Col className="p-0">
                <div>
                  <h2
                    className="p-2 mb-0"
                    style={{
                      color: "white",
                      backgroundColor: "rgba(0,0,0,0.6)",
                      borderRadius: "5px",
                      float: "left",
                    }}
                  >
                    Time Attendance Record
                  </h2>
                </div>
              </Col>
            </Row>

            <Row className="mt-2">
              <Col xs={12} md={6} xl={6} className="mb-2 pl-0 pr-1">
                <div
                  className="p-2"
                  style={{
                    backgroundColor: "rgba(0,0,0,0.6)",
                    borderRadius: "5px",
                    height: "100%",
                    color: "white",
                  }}
                >
                  <Row>
                    <Col md={12} lg={4} className="m-auto pr-0">
                      <div
                        className="pl-1 pb-3 pt-1"
                        style={{ margin: "auto" }}
                      >
                        <b>{Time}</b>
                      </div>
                    </Col>

                    <Col md={12} lg={4} className="mb-2">
                      <div style={{ width: "100%" }}>
                        {/* {selectLocation()} */}
                        {selectLocation_MUI(setTimeAttendance)}
                      </div>
                    </Col>

                    <Col md={12} lg={4}>
                      <div style={{ width: "100%" }} className="pt-1 mb-2">
                        <Button
                          variant="primary"
                          style={{
                            width: "100%",
                            backgroundColor: "#3b5998",
                            border: "0px",
                            height: "100%",
                          }}
                          onClick={HandleCheckIn}
                        >
                          <i className="fas fa-check-circle"></i>
                          &nbsp; CheckIn/Out
                        </Button>
                      </div>
                    </Col>
                  </Row>

                  <Row>
                    <Col>
                      <div>{tableTimeAttenToday()}</div>
                    </Col>
                  </Row>
                </div>
              </Col>

              <Col xs={12} md={6} xl={6} className="mb-2 pl-1 pr-0">
                <div
                  className="p-2"
                  style={{
                    backgroundColor: "rgba(0,0,0,0.6)",
                    borderRadius: "5px",
                    height: "auto",
                    color: "white",
                  }}
                >
                  <Row>
                    <Col md={12} lg={12} className="m-auto">
                      <div className="pl-1 pb-4 pt-2">
                        <b>Time Attendance History</b>
                        &nbsp;
                        <i className="fas fa-history"></i>
                      </div>
                    </Col>
                  </Row>

                  <Row>
                    <Col>
                      <div style={{ borderRadius: "5px" }}>
                        {TableHistory()}
                      </div>
                    </Col>
                  </Row>
                </div>
              </Col>
            </Row>
          </Container>
        </div>
      </div>
    </div>
  );
};

export default TimeAttendancePage;
